/* Hamburger menu */
.menu-icon {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: translate(-100%, 70%);
          transform: translate(-100%, 70%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  cursor: pointer;
}

.line {
  width: 100%;
  height: 3px;
  background-color: black;
  transition: 0.5s;
  background-color: white;
}

.line-1 {
  -webkit-transform: rotate(-45deg) translate(-8px, 6px);
          transform: rotate(-45deg) translate(-8px, 6px);
}
.line-2 {
  opacity: 0;
}
.line-3 {
  -webkit-transform: rotate(45deg) translate(-8px, -6px);
          transform: rotate(45deg) translate(-8px, -6px);
}

/* Navbar */

.navbar {
  position: fixed;
  height: 80px;
  width: 100%;
  background-color: black;
  box-shadow: 0 0 20px black;
  z-index: 1;
}

.navbar-container {
  height: 80px;
  max-width: 1400px;
}

.nav-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 100%;
  left: -100%;
  transition: 0.5s;
  opacity: 0;
  background-color: #1f1f1f;
  border-right: 5px black;
  box-shadow: inset 0 0 5px black;
}

.nav-menu.active {
  left: 0;
  top: 100%;
  opacity: 1;
  height: calc(100vh - 80px);
}

.navbar ul {
  text-decoration: none;
}

.navbar li {
  list-style: none;
  display: block;
  width: 100%;
  cursor: pointer;
  text-align: center;
}

.li-item {
  font-size: 30px;
  text-decoration: none;
  height: 100%;
  display: block;
  padding: 30px;
  font-weight: 700;
  color: white;
}

@media (min-width: 1366px) and (orientation: landscape) {
  .menu-icon {
    opacity: 0;
    height: 0;
    width: 0;
  }

  .navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
  }

  .navbar-container {
    margin-left: 500px;
  }
  .nav-menu {
    -webkit-flex-direction: row;
            flex-direction: row;
    position: relative;
    top: auto;
    left: auto;
    height: 100%;
    width: 100%;
    opacity: 1;
    background-color: black;
    box-shadow: none;
  }
  .nav-menu.active {
    left: 0;
    top: 0;
    opacity: 1;
    height: 80px;
    width: auto;
  }
  .li-item {
    font-size: 20px;
    text-decoration: none;
    width: 100px;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    font-weight: 700;
    color: white;
    margin: 0 50px;
    height: 80px;
    transition: 0.5s;
  }

  .li-item:hover {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

  .nav-menu li {
    cursor: auto;
  }

  .li-item.active {
    border-bottom: 2px solid white;
  }

  .li-item.active:hover {
    -webkit-transform: none;
            transform: none;
  }
}

